.b-sidebar-subscribe {
  margin-bottom: 25px; }
  .b-sidebar-subscribe__title {
    margin-bottom: 5px;
    font-weight: bold; }
  .b-sidebar-subscribe__note {
    margin-top: 5px;
    margin-bottom: 10px;
    font-size: 12px;
    position: relative;
    padding: 10px 0;
    color: #676d72; }
  .b-sidebar-subscribe__note-icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    display: none;
    color: #7aa104;
    padding-right: 40px; }
  .b-sidebar-subscribe__note-icon .icon-svg {
    font-size: 22px; }
  .b-sidebar-subscribe__form {
    margin-top: 10px;
    margin-bottom: 10px; }
  .b-sidebar-subscribe__form .input-group-btn {
    position: relative; }
  .b-sidebar-subscribe__form .icon-svg {
    position: absolute;
    z-index: 3;
    font-size: 20px;
    left: -1px;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    margin: auto;
    cursor: pointer;
    color: #fff; }
  .b-sidebar-subscribe__form .icon-svg:hover + .btn {
    color: #fff;
    background-color: #546f03;
    border-color: #546f03; }
  .b-sidebar-subscribe .btn {
    min-width: 40px;
    height: 40px; }
  @media (min-width: 992px) {
    .b-sidebar-subscribe__note-icon {
      display: block; }
    .b-sidebar-subscribe__note {
      padding-left: 35px; } }
